/* ─── Animated Counter ───────────────────────────────────────────── */
function AnimatedCounter({ target, suffix = '' }: { target: number; suffix?: string }) {
  const [count, setCount] = useState(0);
  const ref = useRef(null);
  const inView = useInView(ref, { once: true });
  useEffect(() => {
    if (!inView) return;
    let v = 0;
    const step = target / (1800 / 16);
    const t = setInterval(() => {
      v += step;
      if (v >= target) { setCount(target); clearInterval(t); }
      else setCount(v);
    }, 16);
    return () => clearInterval(t);
  }, [inView, target]);
  return <span ref={ref}>{Math.round(count)}{suffix}</span>;
}

/* ─── Radial progress ────────────────────────────────────────────── */
function RadialProgress({ pct, label, color = '#b91c1c', size = 80 }: {
  pct: number; label: string; color?: string; size?: number;
}) {
  const ref = useRef(null);
  const inView = useInView(ref, { once: true });
  const r = (size - 10) / 2;
  const circ = 2 * Math.PI * r;
  return (
    <div ref={ref} className="flex flex-col items-center gap-2">
      <div className="relative">
        <svg width={size} height={size} viewBox={`0 0 ${size} ${size}`}>
          <circle cx={size/2} cy={size/2} r={r} fill="none" stroke="#27272a" strokeWidth="5" />
          <motion.circle
            cx={size/2} cy={size/2} r={r}
            fill="none" stroke={color} strokeWidth="5" strokeLinecap="round"
            strokeDasharray={circ}
            strokeDashoffset={circ}
            transform={`rotate(-90 ${size/2} ${size/2})`}
            animate={inView ? { strokeDashoffset: circ - (pct/100) * circ } : {}}
            transition={{ duration: 1.4, ease: 'easeOut', delay: 0.2 }}
          />
        </svg>
        <span className="absolute inset-0 flex items-center justify-center text-xs font-black text-white">{pct}%</span>
      </div>
      <p className="text-[9px] uppercase tracking-widest text-zinc-500 text-center">{label}</p>
    </div>
  );
}

